* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.site-title {
    font-size: 2rem;
    font-weight: bolder;
    font-family: "Monument Extended";
    color: aliceblue;
    z-index: 999;
}

.container {
    margin: 1rem;
    text-align: center;
}

.nav {
    background-color: #3d5a80;
    color: #8ecae6;
    display: flex;
    justify-content: space-between;    
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem
}

.nav li {
    border-radius: 0.5rem;
    padding: 0.25rem;
}

.nav li.active {
    background-color: #293241;
}

.nav li:hover {
    background-color: #293241;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: block;
    align-items: center;
    padding: .25rem;
}

.nav i {
    color: #8ecae6;
}

.toggle-button {
    position: absolute;
    top: 0.75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 22px;
    cursor: pointer;
    z-index: 999;
}

.bar {
    height: 3px;
    width: 100%;
    background-color: #88c1db;
    border-radius: 10px;
}

@media (max-width: 600px) {
    .toggle-button{
        display: flex;
    }

    .nav .expanded {
        /* display:  none; */
        width: 100%;
    }

    .nav {
        flex-direction: column;
        align-items: flex-start;
    }

    .nav ul{
        width: 100%;
        flex-direction: column;
        gap: 0.15rem;
    }

    .nav li {
        text-align: center;
    }

    .nav.active {
        display: flex;
    }

    .nav.nav.active{
        display: flex;
    }

    .expanded {
    position: absolute;
    background-color: #42618af5;
    z-index: 508;
    top: -100%;
    left: 0;
    width: 100%;
    /* height: 50vh; */
    overflow: hidden;
    padding: 4em;
    transition: all 2s cubic-bezier(0.16, 1, 0.3, 1);
    }
}